.profile {
  border: 2px solid rgb(228, 213, 213);
  width: 200px;
  height: 320px;
  border-radius: 15px;
  background: radial-gradient(#edf1cf, #43acb4);
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.description {
  display: flex;
  flex-direction: column;
  padding: 10px;
}
.avatar {
  display: block;
  margin: 0 auto;
  margin-bottom: 15px;
}
.name {
  text-align: center;
  font-weight: 800;
  font-size: 20px;
  margin-bottom: 5px;
}
.tag {
  text-align: center;
  color: #767777;
  font-weight: 400;
  font-size: 12px;
  margin-bottom: 5px;
}
.location {
  text-align: center;
  color: #767777;
  font-weight: 400;
  font-size: 12px;
}
p {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen,
    Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}
.stats {
  text-align: center;
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-around;
  background-color: rgb(245, 245, 245);
  border-top: 1px solid #cbcbcb;
}

.statsItem {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 5px 12px;
  align-items: center;
}

.label {
  color: #767777;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen,
    Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
  font-weight: 200;
  font-size: 13px;
  margin-bottom: 3px;
}

.quantity {
  color: rgb(32, 31, 31);
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen,
    Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
  font-weight: 600;
  font-size: 13px;
  margin-bottom: 3px;
}
